import React from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import styled from 'styled-components';

const StyledIFrame = styled.iframe`
    margin: 0px 20vw; 
    border: 0;
    width: 60vw;
    height: calc(60vw / 16 * 9); 
    frameborder: 0;
    scrolling: no;

    @media screen and (max-width: 1400px) {
        margin: 0px 10vw; 
        width: 80vw;
        height: calc(80vw / 16 * 9); 
    }

    @media screen and (max-width: 880px) {
        margin: 0px 5vw; 
        width: 90vw;
        height: calc(90vw / 16 * 9); 
    }
`;

const Youtube = () => {
    return (
        <StyledIFrame src="https://calendar.google.com/calendar/embed?height=600&amp;wkst=1&amp;bgcolor=%23ffda59&amp;ctz=America%2FToronto&amp;src=bDYzZHFkc2JqajFia2dhc20zYmI4aGxydDYzcWk2b2tAaW1wb3J0LmNhbGVuZGFyLmdvb2dsZS5jb20&amp;color=%23009688&amp;showTabs=0&amp;showPrint=0&amp;showCalendars=0&amp;showTz=1&amp;showTitle=0&amp;showDate=1&amp;showNav=1"></StyledIFrame>
    );
};

export default Youtube;
