import React from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import styled from 'styled-components';

const StyledIFrame = styled.iframe`
    margin: 0px 20vw; 
    border: 0;
    width: 60vw;
    height: calc(60vw / 16 * 9); 
    frameborder: 0;
    scrolling: no;

    @media screen and (max-width: 1400px) {
        margin: 0px 10vw; 
        width: 80vw;
        height: calc(80vw / 16 * 9); 
    }

    @media screen and (max-width: 880px) {
        margin: 0px 5vw; 
        width: 90vw;
        height: calc(90vw / 16 * 9); 
    }
`;

const Calendar = () => {
    return (
        <StyledIFrame src="https://www.youtube.com/embed/okyMHk0mFDw" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; fullscreen; picture-in-picture" allowfullscreen></StyledIFrame>
    );
};

export default Calendar;
